/* eslint-disable no-console */
import { postUserLogout } from '../api/account'
let Kakao = window.Kakao

const KAKAO_JS_KEY = process.env.REACT_APP_KAKAO_JS_KEY
const SDK_URL = 'https://developers.kakao.com/sdk/js/kakao.js'

//  SDK 
export function getKakaoSdk() {
  const script = document.createElement('script')
  script.src = SDK_URL
  script.onload = initKakaoSdk
  script.onError = () => {
    console.error('Kakao SDK  ')
  }
  document.body.append(script)
}

// SDK   callback . 10     
function waitSdk(callback) {
  let to
  const itv = setInterval(() => {
    if (window.Kakao) {
      Kakao = window.Kakao
      callback()
      clearInterval(itv)
      clearTimeout(to)
    }
  }, 500)
  to = setTimeout(() => {
    console.error('Kakao SDK  ')
    clearInterval(itv)
    clearTimeout(to)
  }, 10 * 1000)
}

// SDK     
export function initKakaoSdk() {
  waitSdk(() => {
    Kakao.init(KAKAO_JS_KEY)
    if (Kakao.isInitialized()) console.log('Kakao SDK  ')
    if (!Kakao.isInitialized()) console.error('Kakao SDK  ')
  })
}

//     
export function kakaoAppLogin(callback, errorCallback) {
  waitSdk(() => {
    Kakao.Auth.login({
      success(res) {
        // console.log(' api ', res)
        callback && callback()
      },
      fail(error) {
        console.error('kakao login fail', error)
        window.alert('   ' + JSON.stringify(error))
        errorCallback && errorCallback()
      },
    })
  })
}

//   
export const getKakaoUser = () => {
  return new Promise((resolve, reject) => {
    waitSdk(() => {
      Kakao.API.request({
        url: '/v2/user/me',
        success(response) {
          resolve(response)
        },
        fail(error) {
          console.error(error)
          reject(error)
        },
      })
    })
  })
}

//  
export const logoutKakao = () => {
  return new Promise((resolve, reject) => {
    waitSdk(() => {
      try {
        if (!Kakao.Auth.getAccessToken()) {
          console.log('Not logged in.')
          return
        }
        Kakao.Auth.logout(function () {
          resolve('logout success')
        })
      } catch (e) {
        reject('logout error')
      }
    })
  })
}

//   
export const unlinkKakao = () => {
  return new Promise((resolve, reject) => {
    waitSdk(() => {
      if (!Kakao.Auth.getAccessToken()) {
        console.log('Not logged in.')
        return reject('Not logged in.')
      }
      Kakao.API.request({
        url: '/v1/user/unlink',
        success: function (response) {
          resolve(response)
        },
        fail: function (error) {
          reject(error)
        },
      })
    })
  })
}

// BE,  
export const logoutUser = async () => {
  try {
    await postUserLogout()
  } catch (e) {
    window.alert('BE  ')
  }
  try {
    await unlinkKakao()
  } catch (e) {
    console.log(' unlink ')
  }
}
