import styled from '@emotion/styled'
import { QueryClient, QueryClientProvider } from '@tanstack/react-query'
import React, { Suspense, useEffect, useState } from 'react'
import { Route, Routes, useLocation, useNavigate } from 'react-router-dom'
import { getUser, postUserCheckToken } from './api/account'
import LoadingDim from './component/LoadingDim/LoadingDim'
import TypesPage from './page/TypesPage'
import { Colors } from './util/Colors'

/**
 *  .
 *     .
 *     .
 */

const HomePage = React.lazy(() => import('./page/HomePage'))
const LoginPage = React.lazy(() => import('./page/LoginPage'))
const SheetPage = React.lazy(() => import('./page/SheetPage'))
const JoinPage = React.lazy(() => import('./page/JoinPage'))

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      suspense: true,
      useErrorBoundary: true,
    },
  },
})

function App() {
  const location = useLocation()
  const navigate = useNavigate()
  //     
  const [username, setUsername] = useState<string>()
  const [loading, setLoading] = useState<boolean>()

  useEffect(() => {
    if (navigate && location.pathname) {
      // console.log('App. pathname:', location.pathname)
      setLoading(true)
      postUserCheckToken()
        .then((res) => {
          if (res.data) {
            // console.log('checkToken.  ')
            getUser().then((res) => {
              // console.log('getUser.  . pathname ')
              setUsername(res.data.username)
              if (location.pathname === '/login')
                navigate('/', { replace: true })
              else navigate(location.pathname, { replace: true })
            })
          } else {
            // console.log('checkToken.  ')
            navigate('/login', { replace: true })
          }
        })
        .catch((e) => {
          // console.log('checkToken.  .  ')
          navigate('/login', { replace: true })
        })
        .finally(() => {
          setLoading(false)
        })
    }
  }, [location.pathname, navigate])

  return (
    <QueryClientProvider client={queryClient}>
      <Suspense fallback={<LoadingDim loading={true} />}>
        <MobileWrapper>
          {username && <UsernameDiv>{username}</UsernameDiv>}
          <Routes>
            <Route path='' element={<HomePage />} />
            <Route path='login' element={<LoginPage />} />
            <Route path='sheet/:sheetId' element={<SheetPage />} />
            <Route path='types' element={<TypesPage />} />
            <Route path='join' element={<JoinPage />} />
            <Route path='*' element={<HomePage />} />
          </Routes>
        </MobileWrapper>
        <FakeInput className='fakeInput' />
        <FakeSelect className='fakeSelect'>
          <option></option>
        </FakeSelect>
        <LoadingDim loading={loading} />
      </Suspense>
    </QueryClientProvider>
  )
}

export default App

const UsernameDiv = styled.div`
  position: fixed;
  z-index: 1;
  top: 10px;
  right: 10px;
  @media (min-width: 500px) {
    right: calc(50% - 240px);
  }
  font-size: 12px;
`
const MobileWrapper = styled.div`
  position: relative;
  background: #fff;
  width: 100%;
  height: 100%;
  max-width: 500px;
  margin: auto;
  padding: calc(27.5px + 20px) 20px 10px;
  overflow: auto;
  font-size: 14px;
  @media (min-width: 500px) {
    border: 1px solid ${Colors.greenLine};
  }
`
const FakeInput = styled.input`
  position: fixed;
  border: 0;
  padding: 0 2px;
  outline: none;
  opacity: 0;
  height: 0;
  width: 0;
  font: inherit;
`
const FakeSelect = styled.select`
  position: fixed;
  border: 0;
  padding: 0 2px;
  outline: none;
  opacity: 0;
  height: 0;
  font: inherit;
`
